import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { faBan } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'app-terms-and-conditions',
  templateUrl: './terms-and-conditions.component.html',
  styleUrls: ['./terms-and-conditions.component.less']
})
export class TermsAndConditionsComponent implements OnInit {
  showVideo = true;

  public YT: any;
  public video: any;
  public player: any;

  videoEnded = false;
  termsConditionsChecked = false;
  faBan = faBan;

  constructor(private ref: ChangeDetectorRef) { }

  init() {
    if (window['YT']) {
      this.startVideo();
      return;
    }

    var tag = document.createElement('script');
    tag.src = 'https://www.youtube.com/iframe_api';
    var firstScriptTag = document.getElementsByTagName('script')[0];
    firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);

    window['onYouTubeIframeAPIReady'] = () => this.startVideo();
  }

  ngOnInit() {
    this.video = 'nRiOw3qGYq4';
    this.init();
  }

  startVideo() {
    this.player = new window['YT'].Player('player', {
      videoId: this.video,
      width: '100%',
      playerVars: {
        autoplay: 1,
        modestbranding: 1,
        controls: 1,
        disablekb: 1,
        rel: 0,
        showinfo: 0,
        fs: 0,
        playsinline: 1
      },
      events: {
        'onStateChange': this.onPlayerStateChange.bind(this),
        'onReady': this.onPlayerReady.bind(this),
      }
    });
  }

  onPlayerReady(event) {
    event.target.mute();
    event.target.playVideo();
  }

  onPlayerStateChange(event) {
    console.log(event)
    switch (event.data) {
      case window['YT'].PlayerState.ENDED:
        console.log('ended ');
        this.videoEnded = true;
        this.ref.detectChanges();
        break;
    };
  };

  updateCheckboxStatus(checkBoxChecked) {
    this.termsConditionsChecked = checkBoxChecked;
  }

  sendActivationSuccess(){
    window.opener.postMessage('MS_TEAMS_ACTIVATION_SUCCESS','http://127.0.0.1:5500/index.html');
  }

  sendActivationCancel(){
    window.opener.postMessage('MS_TEAMS_ACTIVATION_CANCEL','http://127.0.0.1:5500/index.html');
  }

}
